/************************************Page Not Found****************************************************/
.pageNotFound{
    height: 88vh;
    background: #000000;
    text-align: center;
    color: #E0E0E0;
    font-family: 'Fira Mono', monospace;
}
.pagenotfoundh1{
    font-size: 2.5rem;
    font-family: 'Permanent Marker', cursive;
}
.pagenotfounddiv{
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
.pagenotfoundsvg{
    width: clamp(300px , 70% , 600px);
    height: 500px;
  
}
#rocket{
    
    -webkit-transform: translateY(750px);
    
            transform: translateY(750px);
   
    -webkit-animation: launch 2s ease-out forwards;
   
            animation: launch 2s ease-out forwards;
}

@-webkit-keyframes launch {
    from {
        -webkit-transform: translateY(750px);
                transform: translateY(750px);
    }
    to{
        -webkit-perspective: 500px;
                perspective: 500px;
        -webkit-transform: translateY(0px);
                transform: translateY(0px); 
    }
}

@keyframes launch {
    from {
        -webkit-transform: translateY(750px);
                transform: translateY(750px);
    }
    to{
        -webkit-perspective: 500px;
                perspective: 500px;
        -webkit-transform: translateY(0px);
                transform: translateY(0px); 
    }
}
#stars {
    -webkit-animation: twinkling 2s linear ;
            animation: twinkling 2s linear ;
}
@-webkit-keyframes twinkling {

    from{
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@keyframes twinkling {

    from{
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
.pagenotfoundtext{
    opacity: 0;
    -webkit-animation:appear 1s ease-in forwards;
            animation:appear 1s ease-in forwards;
    -webkit-animation-delay: 1.8s;
            animation-delay: 1.8s;
    margin-top: -186px !important;
    text-align: center;
    color: white;
}
@-webkit-keyframes appear {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
@keyframes appear {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
.pagenotfoundAnchor{
    color:#F66947;
    text-decoration: none;
}
/**********************************Page Not Found End*********************************************************/


